import styles from "./Profile.module.css";
import { Link, useHistory } from "react-router-dom";
import { useCallback, useRef, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  Input,
  Button,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { setProfileValue } from "../../services/actions/profileActions";
import {
  getProfileInfo,
  logOutRequest,
  patchProfileInfo,
} from "../../utils/api";

function Profile() {
  const nameRef = useRef(null);
  const loginRef = useRef(null);
  const passwordRef = useRef(null);
  const onNameIconClick = () => {
    setTimeout(() => nameRef.current.focus(), 0);
  };
  const onLoginIconClick = () => {
    setTimeout(() => loginRef.current.focus(), 0);
  };
  const onPasswordIconClick = () => {
    setTimeout(() => passwordRef.current.focus(), 0);
  };

  const dispatch = useDispatch();
  const history = useHistory();

  const { name, login, password } = useSelector((store) => store.profile);

  useEffect(() => {
    dispatch(getProfileInfo());
  }, [dispatch]);

  const onFormChange = (e) => {
    dispatch(setProfileValue(e.target.name, e.target.value));
  };

  const onFormSubmit = useCallback(
    (e) => {
      e.preventDefault();
      dispatch(patchProfileInfo(name, login, password));
    },
    [name, login, password, dispatch]
  );

  const onFormReset = useCallback(() => {
    dispatch(getProfileInfo());
  }, [dispatch]);

  const onLogOut = useCallback(() => {
    dispatch(logOutRequest(history));
  }, [history]);

  return (
    <main className={styles.section}>
      <div className={styles.container}>
        <div className={styles.links}>
          <Link to={"/profile"} className={styles.link}>
            
          </Link>
          <Link to={"/"} className={styles.link}>
             
          </Link>
          <Link to={"/login"} className={styles.link} onClick={onLogOut}>
            
          </Link>
          <p className={styles.text}>
                    
          </p>
        </div>
        <form className={styles.form} onSubmit={onFormSubmit}>
          <div className={styles.input}>
            <Input
              type={"text"}
              placeholder={""}
              onChange={onFormChange}
              icon={"EditIcon"}
              value={name}
              name={"name"}
              error={false}
              onIconClick={onNameIconClick}
              errorText={""}
              size={"default"}
              ref={nameRef}
            />
          </div>
          <div className={styles.input}>
            <Input
              type={"email"}
              placeholder={""}
              onChange={onFormChange}
              icon={"EditIcon"}
              value={login}
              name={"login"}
              error={false}
              onIconClick={onLoginIconClick}
              errorText={""}
              size={"default"}
              ref={loginRef}
            />
          </div>
          <div className={styles.input}>
            <Input
              type={"password"}
              placeholder={""}
              onChange={onFormChange}
              icon={"EditIcon"}
              value={password}
              name={"password"}
              error={false}
              onIconClick={onPasswordIconClick}
              errorText={""}
              size={"default"}
              ref={passwordRef}
            />
          </div>
          <div className={styles.buttons}>
            <div className={styles.button}>
              <Button type="primary" size="medium">
                
              </Button>
            </div>
            <div className={styles.button}>
              <Button
                type="primary"
                size="medium"
                onClick={onFormReset}
                htmlType="reset"
              >
                
              </Button>
            </div>
          </div>
        </form>
      </div>
    </main>
  );
}

export default Profile;
